import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <div class="container">
          <header class="blog-header py-3">
            <div class="row flex-nowrap justify-content-between align-items-center">
              <div class="col-4 pt-1">
              </div>
              <div class="col-4 text-center">
              </div>
              <div class="col-4 d-flex justify-content-end align-items-center">
                <a class="btn btn-sm btn-outline-secondary" href="#">Sign up</a>
              </div>
            </div>
          </header>

          <div class="row no-gutters border rounded overflow-hidden flex-md-row mb-4 shadow-sm position-relative">
            <div class="col p-4 d-flex flex-column position-static">
              <h3 class="mb-0">Dream Book</h3>
              <p class="card-text mb-auto">Καλωσήλθες στο Dream Book, όπου το μόνο όριο είναι η φαντασία σου! Ψάξε ιδέες για διάβασμα, συζήτησε τις αγαπημένες σου ιστορίες με τους φίλους σου ή γράψε τις δικές σου!</p>
              <button type="button" class="btn btn-secondary">Είσοδος</button>
            </div>
            <div class="col-auto d-none d-lg-block">
              <img src="main.jpg" width="857" height="821" class="float-right" alt="Blah"></img>
            </div>
          </div>

        </div>
      </div>
    );
  }
}

export default App;



